body{margin:0;font-family: "CNN","CNN Sans Display","CNN Sans","Helvetica Neue",Helvetica,Utkal,Arial,sans-serif;}

h1{color:#4b5363;font-size:42px;text-align:center}

.card{margin:100px auto 2em;
    position:relative;
    box-shadow:0 3px 18px 3px rgba(46,12,197,.2);
    width:480px;
    font-family:arial;
    padding:0;
    height:750px;
    background-color:#fff;
    border-radius:16px;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center}
    
    .card_content{
        width:100%;
    height:100%}
    
    .introImage{width:480px;
        height:750px;
        object-fit:cover;
        border-radius:16px 16px 16px 16px}

    .introDiv{position:absolute;
        width:400px;
        height:100px;
        font-size:2em;
        text-align:center;
        left:0;top:0;
        right:0;
        bottom:0;margin:auto}

        .introButton{background-color:#efefef;
            color:#333;
            width:200px;
            font-size:12px;
            height:auto;margin-top:.5em;
            border-radius: 16px;
        }

        .introButton:hover{
            background-color:#81a8f8;
        color: white;}
        
        .introDiv p{
            font-size:16px}
            
        .graphicCredit{color:grey;text-align:center}
        
        .factsDiv{display:flex;flex-direction:column}

        .previousButton{
          
            margin-top: 1em;
            background-color: none;
            border: none;
            font-size: .8em;
            
            text-decoration: underline;
            cursor: pointer;
        }

        .slideImage{display:block;width:100%;height:100%;margin:0 auto 5px;background-color:#fff;object-fit:cover;border-radius:16px 16px 0 0}
        
        .cardHeadline{color:#000;
            font-size:30px;
            font-weight:900;
            text-align:center;
            padding:0;
            margin:4px 2px;}
        
        .cardDescription{
            margin:0 auto;
            font-size: 1em;
            padding: 5px;
            line-height:1.5}

        .imageCredit{text-align:center}
        .cardCaption{padding:.5em;margin:0;color:#201f1f;font-size:12px}.questionCounter{padding:0;margin:0}ul{list-style:none}ul li:before{content:"\2022";width:1.2em;display:inline-block;color:red}.buttonDiv{margin-top:.5em}.buttonDiv,.buttonOptions{display:flex;flex-direction:column;align-items:center}.buttonDiv .quizButtonGroup button:hover{color:blue}.buttonDiv button{margin-top:.4em;width:300px;height:30px}
        .nextButton{font-size:.8em;border-radius:5px;}.nextButton:hover,.previousButton:hover{background-color:#81a8f8;color:#fff}.correct{background-color:#00f}.previousButton{text-decoration:none;cursor:pointer;border:none}.buttonOptions{margin-top:1em;display:flex;flex-direction:row;width:150px;height:auto}.buttonOptions button:hover{background-color:#d8ced8}

        .quizButtonGroup{
            display: flex;
            flex-direction: column;
        }

        .quizButtons{
            border-radius: 5px;
        }
        .quizNavigation{
            display: flex;
            width: 200px;
            margin-top: 1em;
        }

        .quizNavigation button:hover{
            color: white;
            background-color: gray;
        }

        .hurricaneCategories li{
            padding-top: .7em;
            
        }

        .finalButtonDiv{
            display: flex;
            flex-direction: column;
        }

        .finalButtonDiv button{
           width: 200px;
        }